import React from 'react';
import * as XLSX from 'xlsx';
import { saveAs } from 'file-saver';

export const DataTransfer = ({ onImport, onExport, type }) => {
  const handleImport = (event) => {
    const file = event.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        try {
          const workbook = XLSX.read(e.target.result, { type: 'array' });
          const firstSheetName = workbook.SheetNames[0];
          const worksheet = workbook.Sheets[firstSheetName];
          
          const rawData = XLSX.utils.sheet_to_json(worksheet, {
            defval: '',
            raw: true,
          });

          if (type === 'orders') {
            // 
            const processedData = rawData.map(row => ({
              id: String(Date.now() + Math.random()),
              date: row[''] ? new Date(row['']).toISOString() : new Date().toISOString(),
              items: [{
                specs: row[''] || '',
                quantity: Number(row['']) || 0,
                sellingPrice: Number(row['']) || 0,
                isRemoteArea: false,
                productId: row[''] || ''
              }],
              totalAmount: Number(row['']) || 0,
              totalProfit: Number(row['']) || 0,
              shippingFee: Number(row['']) || 0
            }));
            onImport(processedData);
          } else {
            // 
            const processedData = rawData.map(row => ({
              id: String(Date.now() + Math.random()),
              date: row[''] || new Date().toISOString(),
              name: row[''] || '',
              cost: Number(row['']) || 0,
              promotionFee: Number(row['']) || 0,
              description: row[''] || '',
              inventory: Number(row['']) || 0
            }));
            onImport(processedData);
          }
        } catch (error) {
          alert('');
          console.error(error);
        }
      };
      reader.readAsArrayBuffer(file);
    }
  };

  const handleExport = () => {
    const data = onExport();
    if (!data || data.length === 0) {
      alert('');
      return;
    }

    let exportData;
    let fileName;
    let sheetName;
    let colWidths;

    if (type === 'orders') {
      // 
      exportData = data.map(order => ({
        '': new Date(order.date).toLocaleDateString('zh-CN'),
        '': order.items[0]?.specs || '',
        '': order.items[0]?.quantity || 0,
        '': order.items[0]?.sellingPrice || 0,
        '': order.shippingFee || 0,
        '': order.totalProfit || 0
      }));
      fileName = '';
      sheetName = '';
      colWidths = [
        { wch: 12 },  // 
        { wch: 30 },  // 
        { wch: 8 },   // 
        { wch: 10 },  // 
        { wch: 8 },   // 
        { wch: 10 }   // 
      ];
    } else {
      // 
      exportData = data.map(product => ({
        '': new Date(product.date).toLocaleDateString('zh-CN'),
        '': product.name,
        '': product.cost,
        '': product.promotionFee,
        '': product.description,
        '': product.inventory
      }));
      fileName = '';
      sheetName = '';
      colWidths = [
        { wch: 12 },  // 
        { wch: 30 },  // 
        { wch: 10 },  // 
        { wch: 10 },  // 
        { wch: 20 },  // 
        { wch: 8 }    // 
      ];
    }

    // 
    const wb = XLSX.utils.book_new();
    const ws = XLSX.utils.json_to_sheet(exportData);
    ws['!cols'] = colWidths;
    XLSX.utils.book_append_sheet(wb, ws, sheetName);

    // Excel
    const excelBuffer = XLSX.write(wb, { bookType: 'xlsx', type: 'array' });
    const blob = new Blob([excelBuffer], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' });
    
    // 
    saveAs(blob, `${fileName}_${new Date().toISOString().split('T')[0]}.xlsx`);
  };

  return (
    <div className="data-transfer">
      <div className="import-wrapper">
        <input
          type="file"
          accept=".xlsx,.xls"
          onChange={handleImport}
          id={`import-${type}`}
          className="file-input"
        />
        <label htmlFor={`import-${type}`} className="import-btn">
          {type === 'orders' ? '' : ''}
        </label>
      </div>
      <button onClick={handleExport} className="export-btn">
        {type === 'orders' ? '' : ''}
      </button>
    </div>
  );
}; 