import React, { useState, useEffect, useMemo } from 'react';
import { storage } from '../utils/storage';
import { OrderForm } from './OrderForm';
import { Statistics } from './Statistics';
import { DataTransfer } from './DataTransfer';
import { Dashboard } from './Dashboard';

export const OrderList = () => {
  const [orders, setOrders] = useState([]);
  const [products, setProducts] = useState([]);
  const [showForm, setShowForm] = useState(false);
  const [filters, setFilters] = useState({
    dateRange: { start: null, end: null },
    specs: '',
    minProfit: null,
    maxProfit: null
  });

  // 
  const [currentPage, setCurrentPage] = useState(1);
  const [pageSize, setPageSize] = useState(10);
  const [totalItems, setTotalItems] = useState(0);

  // 
  const [sortConfig, setSortConfig] = useState({
    key: 'date',
    direction: 'desc'
  });

  useEffect(() => {
    setOrders(storage.getOrders() || []);
    setProducts(storage.getProducts() || []);
    
    // 
    const handleProductsUpdate = () => {
      setProducts(storage.getProducts() || []);
    };
    window.addEventListener('productsUpdated', handleProductsUpdate);
    return () => window.removeEventListener('productsUpdated', handleProductsUpdate);
  }, []);

  const handleAddOrder = (order) => {
    const newOrder = {
      ...order,
      id: Date.now().toString(),
      date: new Date(order.date).toISOString()
    };
    const newOrders = [...orders, newOrder];
    setOrders(newOrders);
    storage.saveOrders(newOrders);
    setShowForm(false);
  };

  const formatDate = (dateString) => {
    if (!dateString) return 'Invalid Date';
    const date = new Date(dateString);
    if (isNaN(date.getTime())) return 'Invalid Date';
    return date.toLocaleString('zh-CN', {
      year: 'numeric',
      month: '2-digit',
      day: '2-digit',
      hour: '2-digit',
      minute: '2-digit',
      second: '2-digit',
      hour12: false
    });
  };

  const getProductName = (productId) => {
    const product = products.find(p => p.id === productId);
    return product ? product.name : '';
  };

  // 
  const filteredOrders = useMemo(() => {
    return orders.filter(order => {
      const orderDate = new Date(order.date);
      const dateInRange = (!filters.dateRange.start || orderDate >= new Date(filters.dateRange.start)) &&
                         (!filters.dateRange.end || orderDate <= new Date(filters.dateRange.end));
      
      const specsMatch = !filters.specs || 
                        order.items.some(item => 
                          item.specs.toLowerCase().includes(filters.specs.toLowerCase())
                        );
      
      const profitInRange = (!filters.minProfit || order.totalProfit >= Number(filters.minProfit)) &&
                           (!filters.maxProfit || order.totalProfit <= Number(filters.maxProfit));
      
      return dateInRange && specsMatch && profitInRange;
    });
  }, [orders, filters]);

  // 
  const paginatedOrders = useMemo(() => {
    // 
    const filtered = filteredOrders;
    setTotalItems(filtered.length);

    //  - 
    const sorted = [...filtered].sort((a, b) => {
      if (sortConfig.key === 'date' || !sortConfig.key) {
        return sortConfig.direction === 'asc' 
          ? new Date(a.date) - new Date(b.date)
          : new Date(b.date) - new Date(a.date);
      }
      if (sortConfig.key === 'totalAmount' || sortConfig.key === 'totalProfit') {
        return sortConfig.direction === 'asc'
          ? a[sortConfig.key] - b[sortConfig.key]
          : b[sortConfig.key] - a[sortConfig.key];
      }
      return 0;
    });

    // 
    const start = (currentPage - 1) * pageSize;
    const end = start + pageSize;
    return sorted.slice(start, end);
  }, [filteredOrders, currentPage, pageSize, sortConfig]);

  // 
  const handleSort = (key) => {
    setSortConfig(prev => ({
      key,
      direction: prev.key === key && prev.direction === 'asc' ? 'desc' : 'asc'
    }));
  };

  // 
  const renderSortIcon = (key) => {
    if (sortConfig.key !== key) return '';
    return sortConfig.direction === 'asc' ? '' : '';
  };

  const handleImportOrders = (data) => {
    // 
    const processedData = data.map(row => {
      // 
      if (row.items && Array.isArray(row.items)) {
        return {
          id: String(Date.now() + Math.random()),
          date: row.date || new Date().toISOString(),
          items: row.items,
          totalAmount: row.totalAmount || 0,
          shippingFee: Number(row.shippingFee),
          totalProfit: row.totalProfit || 0,
          isImported: true
        };
      }

      // Excel
      if (!row['']) {
        console.warn(':', row);
        return null;
      }

      // 
      let orderDate;
      try {
        orderDate = row[''] ? new Date(row['']) : new Date();
        if (isNaN(orderDate.getTime())) {
          orderDate = new Date();
        }
      } catch {
        orderDate = new Date();
      }

      return {
        id: String(Date.now() + Math.random()),
        date: orderDate.toISOString(),
        items: [{
          specs: String(row[''] || '').trim(),
          quantity: Number(row['']) || 1,
          sellingPrice: Number(row['']) || 0,
          isRemoteArea: false
        }],
        totalAmount: Number(row['']) * (Number(row['']) || 1),
        shippingFee: Number(row['']),
        totalProfit: Number(row['']) || 0,
        isImported: true
      };
    }).filter(Boolean);

    if (processedData.length === 0) {
      alert('');
      return;
    }

    // 
    setOrders(prevOrders => {
      const newOrders = [...prevOrders, ...processedData];
      storage.saveOrders(newOrders);
      return newOrders;
    });

    // 
    alert(` ${processedData.length} `);
  };

  const handleExportOrders = () => {
    return orders;
  };

  const handleClearData = () => {
    if (window.confirm('')) {
      // 
      setOrders([]);
      setCurrentPage(1);
      setTotalItems(0);
      
      //  storage 
      storage.clearOrders();
      
      // 
      alert('');
    }
  };

  const handleFilterChange = (field, value) => {
    setFilters(prev => ({
      ...prev,
      [field]: value
    }));
  };

  const handleDeleteOrder = (orderId) => {
    if (window.confirm('')) {
      const newOrders = orders.filter(order => order.id !== orderId);
      setOrders(newOrders);
      storage.saveOrders(newOrders);
    }
  };

  const renderOrderRow = (order) => {
    if (order.isImported) {
      // 
      return (
        <tr key={order.id}>
          <td>{new Date(order.date).toLocaleDateString('zh-CN')}</td>
          <td>{order.items[0]?.specs || ''}</td>
          <td>{order.items[0]?.quantity || 0}</td>
          <td>{(order.totalAmount || 0).toFixed(2)}</td>
          <td>{(order.shippingFee || 0).toFixed(2)}</td>
          <td>{(order.totalProfit || 0).toFixed(2)}</td>
          <td>
            <button 
              onClick={() => handleDeleteOrder(order.id)}
              className="delete-btn"
            >
              
            </button>
          </td>
        </tr>
      );
    } else {
      // 
      const totalQuantity = order.items?.reduce((sum, item) => sum + (Number(item.quantity) || 0), 0) || 0;
      
      return (
        <tr key={order.id}>
          <td>{new Date(order.date).toLocaleDateString('zh-CN')}</td>
          <td>{order.items?.map(item => item.specs).join(', ') || ''}</td>
          <td>{totalQuantity}</td>
          <td>{(order.totalAmount || 0).toFixed(2)}</td>
          <td>{(order.shippingFee || 0).toFixed(2)}</td>
          <td>{(order.totalProfit || 0).toFixed(2)}</td>
          <td>
            <button 
              onClick={() => handleDeleteOrder(order.id)}
              className="delete-btn"
            >
              
            </button>
          </td>
        </tr>
      );
    }
  };

  return (
    <div className="order-list">
      {/*  */}
      <Dashboard orders={orders} />
      
      <div className="order-list-header">
        <h2></h2>
        <div className="header-actions">
          <button 
            onClick={() => setShowForm(!showForm)}
            className="add-order-btn"
          >
            {showForm ? '' : '+ '}
          </button>
          <DataTransfer
            onImport={handleImportOrders}
            onExport={handleExportOrders}
            type="orders"
          />
          <button 
            onClick={handleClearData}
            className="clear-data-btn"
          >
            
          </button>
        </div>
      </div>

      {/*  */}
      <div className="filters">
        <div className="filter-group">
          <label></label>
          <input
            type="date"
            value={filters.dateRange.start}
            onChange={e => handleFilterChange('dateRange', { ...filters.dateRange, start: e.target.value })}
          />
          <span></span>
          <input
            type="date"
            value={filters.dateRange.end}
            onChange={e => handleFilterChange('dateRange', { ...filters.dateRange, end: e.target.value })}
          />
        </div>
        <div className="filter-group">
          <label></label>
          <input
            type="text"
            value={filters.specs}
            onChange={e => handleFilterChange('specs', e.target.value)}
            placeholder=""
          />
        </div>
        <div className="filter-group">
          <label></label>
          <input
            type="number"
            value={filters.minProfit}
            onChange={e => handleFilterChange('minProfit', e.target.value)}
            placeholder=""
          />
          <span></span>
          <input
            type="number"
            value={filters.maxProfit}
            onChange={e => handleFilterChange('maxProfit', e.target.value)}
            placeholder=""
          />
        </div>
      </div>

      {showForm && (
        <div className="order-form-container">
          <h3></h3>
          <OrderForm products={products} onSubmit={handleAddOrder} />
        </div>
      )}

      <div className="orders-table">
        <table>
          <thead>
            <tr>
              <th onClick={() => handleSort('date')}>
                 {renderSortIcon('date')}
              </th>
              <th></th>
              <th></th>
              <th onClick={() => handleSort('totalAmount')}>
                 {renderSortIcon('totalAmount')}
              </th>
              <th></th>
              <th onClick={() => handleSort('totalProfit')}>
                 {renderSortIcon('totalProfit')}
              </th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {paginatedOrders.map(renderOrderRow)}
          </tbody>
        </table>

        {/*  */}
        <div className="pagination">
          <div className="page-size">
            
            <select 
              value={pageSize} 
              onChange={(e) => {
                const newPageSize = Number(e.target.value);
                setPageSize(newPageSize);
                // 
                const newMaxPage = Math.ceil(totalItems / newPageSize);
                if (currentPage > newMaxPage) {
                  setCurrentPage(newMaxPage);
                }
              }}
            >
              <option value={10}>10</option>
              <option value={20}>20</option>
              <option value={50}>50</option>
              <option value={100}>100</option>
            </select>
          </div>
          
          <div className="page-controls">
            <button 
              onClick={() => setCurrentPage(1)}
              disabled={currentPage === 1}
            >
              
            </button>
            <button 
              onClick={() => setCurrentPage(prev => prev - 1)}
              disabled={currentPage === 1}
            >
              
            </button>
            <span className="page-info">
               {currentPage}  /  {Math.ceil(totalItems / pageSize)} 
               {totalItems} 
            </span>
            <button 
              onClick={() => setCurrentPage(prev => prev + 1)}
              disabled={currentPage >= Math.ceil(totalItems / pageSize)}
            >
              
            </button>
            <button 
              onClick={() => setCurrentPage(Math.ceil(totalItems / pageSize))}
              disabled={currentPage >= Math.ceil(totalItems / pageSize)}
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}; 